import React, { Component } from 'react';
import './App.css';
import { Switch, Route } from 'react-router-dom';
import Root from './Components/Root/Root';
import Signin from './Components/Signin/Signin';
import Signup from './Components/Signup/Signup';
import Informations from './Components/Informations/Informations';
import Home from './Components/Home/Home';

class App extends Component {
  render() {
    return (
        <div className="App">
          <Switch basename="/daily-measure">
            <Route exact path={`${process.env.PUBLIC_URL}/`} component={Root}/>
            <Route path={`${process.env.PUBLIC_URL}/signin`} component={Signin}/>
            <Route path={`${process.env.PUBLIC_URL}/signup`} component={Signup}/>
            <Route path={`${process.env.PUBLIC_URL}/informations`} component={Informations}/>
            <Route path={`${process.env.PUBLIC_URL}/home`} component={Home}/>
          </Switch>
        </div>
    );
  }
}

export default App;